.App {
  text-align: center;
}

.App-logo {
  width: auto;
  height: 50%;
  object-fit: cover;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body{
  background-image: url(./images/super.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

